interface Item {
    name: string;
    url: string;
    user: string;
    diceValue: string;
}

interface Result {
    ok: string;
    result: string;
}

function ShakeDice(item: Item): Result {
    let result: Result = {
        ok: '',
        result: '',
    };

    let _1d100Value: number = Math.round(Math.random() * 100) + 1;

    if (_1d100Value <= 5) {
        result.ok = '';
    } else if (_1d100Value <= +item.diceValue) {
        result.ok = '';
    } else {
        result.ok = '';
    }

    result.result =
        ':' + item.name + ' ' + _1d100Value + ' <= ' + item.diceValue;

    return result;
}

export default ShakeDice;
